import {useEffect, useRef} from 'react';

const useUpdate = (fn:()=>void, dependency:any[]) => {
  const count = useRef(0);
  useEffect(() => {
    count.current += 1;
  });

//只要tags变化了，就...
  useEffect(() => {
    if (count.current > 1) {
      fn()
    }
  }, [fn, dependency]); // 必须是不可变数据，每次修改必须是新的对象
};

export {useUpdate};